import { createRouter, createWebHistory } from "vue-router";

import Home from "@/views/Home";
import MovieLanding from "@/views/MovieLanding";
import MoviesPage from "@/views/MoviesPage";
import CategoryMoviesPage from "@/views/CategoryMoviesPage";
import SearchPage from "@/views/SearchPage";
import ArticlePage from "@/views/ArticlePage";

import { appRoute, isAppTypeIsProd } from "../constants";

const routes = [
  {
    path: isAppTypeIsProd ? `/${appRoute}/` : "/",
    name: "Home",
    component: Home,
  },
  {
    path: isAppTypeIsProd ? `/${appRoute}/movie` : "/movie",
    name: "Movie",
    component: MoviesPage,
  },
  {
    path: isAppTypeIsProd
      ? `/${appRoute}/movie/category/:category`
      : "/movie/category/:category",
    component: CategoryMoviesPage,
  },
  {
    path: isAppTypeIsProd ? `/${appRoute}/movie/:id` : "/movie/:id",
    component: MovieLanding,
  },
  {
    path: isAppTypeIsProd ? `/${appRoute}/search` : "/search",
    component: SearchPage,
  },
  {
    path: isAppTypeIsProd
      ? `/${appRoute}/article/:uid/:link`
      : "/article/:uid/:link",
    component: ArticlePage,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
