<template>
  <div class="wrapper">
    <div v-if="!entry" class="skeleton">
      <div class="loader"></div>
    </div>
    <div class="content" v-if="entry">
      <div class="image-box">
        <img :src="landingImage" class="image" />
      </div>
      <div class="main-info">
        <div class="info">
          <h1 class="title">{{ entry.title }}</h1>
          <div class="author-box" v-if="articleAuthor">
            <div class="box-image">
              <img :src="articleAuthor.author_image.url" alt="" class="image" v/>
            </div>
            <div class="name">{{ articleAuthor.name }}</div>
            <div class="time">{{ new Date(entry.date).toDateString() }}</div>
          </div>
          <div class="text" v-html="entry.text"></div>
        </div>
        <div class="related" v-if="relatedArticles">
          <h2>Related Articles:</h2>
          <ArticleItem
            v-for="item in relatedArticles"
            :key="item.uid"
            :title="item.title"
            :image="item.image.url"
            :uid="item.uid"
            :link="item.url.toLowerCase()"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from "vue";
import { useRoute } from "vue-router";

import { fetchArticleImage } from "@/queries/article-image";
import { getEntry } from "@/queries/article-page";

import ArticleItem from "../components/ArticleItem";

const route = useRoute();

const entry = ref(null),
  articleAuthor = ref(null),
  relatedArticles = ref([]),
  landingImage = ref(null);

const getArticleData = async (uid) => {
  entry.value = await getEntry(uid);

  articleAuthor.value = await getEntry(
    entry.value.article_author[0].uid,
    "author_article"
  );

  landingImage.value = await fetchArticleImage(entry.value.image.url);

  await getRelatedArticles(entry.value.related_articles);
};

const getRelatedArticles = (articles) => {
  articles.forEach((article) => {
    getEntry(article.uid).then((data) => relatedArticles.value.push(data));
  });
};

getArticleData(route.params.uid);
</script>

